import {createNativeStackNavigator} from '@react-navigation/native-stack';
import {ApplicationProvider, IconRegistry} from '@ui-kitten/components';
import {PaperProvider} from 'react-native-paper';
import * as eva from '@eva-design/eva';
import {EvaIconsPack} from '@ui-kitten/eva-icons';
import {default as theme} from '../../custom-theme.json'; // <-- Import app theme
import {default as mapping} from '../../mapping.json'; // <-- Import app mapping
import Toast from 'react-native-toast-message';
import NavigationNameAccount from '../constants/NavigationNameAccount';
import React from 'react';
import toastConfig from '../utils/ToastConfig';
import HomeScreenHostApp from '../app/home-list-services';
import WithComponent from '../app/components/withComponent';
const Stack = createNativeStackNavigator();

const listScreen = [
  {
    app: 'eRequest',
    moduleName: 'eRequestApp',
    navigationName: 'ERequest',
  },
  {
    app: 'eContract',
    moduleName: 'createContract',
    navigationName: 'eContract',
  },
  {
    app: 'eTasks',
    moduleName: 'App',
    navigationName: 'eTasks',
  },
  {
    app: 'eBLDTApp',
    moduleName: 'eBLDTApp',
    navigationName: 'eBLDT',
  },
];

const HomeListNavigation = (props: any) => {
  return (
    <>
      <IconRegistry icons={[EvaIconsPack]} />
      <ApplicationProvider
        customMapping={mapping as any}
        {...eva}
        theme={{...eva.light, ...theme}}>
        <PaperProvider>
          <Stack.Navigator
            screenOptions={{
              headerShown: false,
            }}>
            <Stack.Screen
              initialParams={props}
              name={NavigationNameAccount.HomeScreen}
              component={HomeScreenHostApp}
            />
            {listScreen.map((screen, index) => (
              <Stack.Screen
                key={screen.navigationName}
                name={screen.navigationName}
                component={WithComponent}
                initialParams={{app: screen.app, moduleName: screen.moduleName}}
              />
            ))}
          </Stack.Navigator>
          <Toast config={toastConfig} />
        </PaperProvider>
      </ApplicationProvider>
    </>
  );
};

export default HomeListNavigation;
