import {NavigationName} from '../../constants';
import {CommonUtils} from '../../utils';
import {
  INotification,
  INotificationItem,
} from '../screens/notification/interface';

export let configScreen = {
  screens: {
    [NavigationName.ERequest]: {path: ''},
    [NavigationName.SigneContractScreen]: {path: ''},
  },
};

export const handleUrl = (url: string | null) => {
  // 1 for my ticket, 2 for ticket required
  if (!url) return null;
  const path = url?.split('?')[0];
  const params = url?.split('?')[1];
  if (!params) return null;
  const listParams = CommonUtils.parseQueryParamsString(params);

  if (listParams._typeView === 'mine') {
    return {
      type: `details_${new Date().getTime()}`,
      dataTicketId: path.split('/').pop(),
      selectTabBottom: 1,
    };
  } else if (listParams._typeView === 'required') {
    return {
      type: `details_${new Date().getTime()}`,
      dataTicketId: path.split('/').pop(),
      selectTabBottom: 2,
      recipientDetailsId: listParams._recipientDetailsId,
      userTaskId: listParams._userTaskId,
      notifyActionType: listParams.from,
    };
  } else return null;
};

export const handleDirectNoti = (item: INotification) => {
  if (
    item.type === 'ticket_create' ||
    item.type === 'ticket_update' ||
    item.type === 'ticket_cancel' ||
    item.type === 'ticket_require_confirm' ||
    item.type === 'ticket_confirm' ||
    item.type === 'ticket_reject' ||
    item.type === 'ticket_require_review' ||
    item.type === 'ticket_review' ||
    item.type === 'ticket_review_reject' ||
    item.type === 'ticket_assign_user' ||
    item.type === 'ticket_update_status' ||
    item.type === 'ticket_authorize' ||
    item.type === 'ticket_remind_workflow' ||
    item.type === 'ticket_remind_usertask' ||
    item.type === 'ticket_remind_workflow_overdue_onetime' ||
    item.type === 'ticket_remind_usertask_overdue_onetime' ||
    item.type === 'ticket_remind_workflow_overdue_period' ||
    item.type === 'ticket_remind_usertask_overdue_period' ||
    item.type === 'ticket_tag_comment' ||
    item.type === 'acl_ticket_create'
  ) {
    const content = JSON.parse(item.content) as INotificationItem;
    if (content) {
      return `kitaDemo://details_${new Date().getTime()}/${content.ticketId}/${
        item.type === 'ticket_tag_comment' || item.type === 'acl_ticket_create'
          ? 1
          : 2
      }/${content.recipientDetailsId}/${content.userTaskId}/${item.type}`;
    }
  }
  return '';

  // if (item.type === 'ticket_delete') {
  //   viewList('ticket', 'delete');
  //   return;
  // }

  // if (
  //   item.type === 'service_group_create' ||
  //   item.type === 'service_group_update'
  // ) {
  //   viewList('service-group', 'update');
  //   return;
  // }

  // if (item.type === 'service_group_delete') {
  //   viewList('service-group', 'delete');
  //   return;
  // }
};
