import {Icon} from '@ui-kitten/components';
import {StyleSheet, View} from 'react-native';
import TextCM from '../../../components/Text'; // Giả định đường dẫn đúng
import {DateUtils} from '../../../../utils'; // Giả định đường dẫn đúng
import {useTranslation} from 'react-i18next';

type Props = {
  type?: 'onsite' | 'checkinout';
  timeCheckinONSITE?: string;
  timeCheckoutONSITE?: string;
  timeCheckinDEFAULT?: string;
  timeCheckoutDEFAULT?: string;
};

const InfoCheckinoutDashboard = ({
  type,
  timeCheckinDEFAULT,
  timeCheckoutDEFAULT,
  timeCheckinONSITE,
  timeCheckoutONSITE,
}: Props) => {
  const {t} = useTranslation();
  const renderInfoCheckinout = (checkType: 'in' | 'out', time?: string) => {
    return (
      <View style={styles.itemDate}>
        {time ? (
          <Icon name="checkmark" width={24} height={24} fill={'#39AC6D'} />
        ) : (
          <Icon
            name="alert-triangle-outline"
            width={24}
            height={24}
            fill={'#febd02'}
          />
        )}

        <TextCM style={styles.txtDate}>
          {time
            ? t('DB.etms.timeCheckSuccess', {
                time: DateUtils.getCurrentTime(time),
                type: checkType === 'in' ? 'in' : 'out',
              })
            : t('DB.etms.noCheckinout', {
                type: checkType === 'in' ? 'vào' : 'ra',
              })}
        </TextCM>
      </View>
    );
  };

  return (
    <View style={{rowGap: 6, flex: 1}}>
      {type === 'onsite' ? (
        <>
          {renderInfoCheckinout('in', timeCheckinONSITE)}
          {renderInfoCheckinout('out', timeCheckoutONSITE)}
        </>
      ) : (
        <>
          {renderInfoCheckinout('in', timeCheckinDEFAULT)}
          {renderInfoCheckinout('out', timeCheckoutDEFAULT)}
        </>
      )}
    </View>
  );
};

export default InfoCheckinoutDashboard;

const styles = StyleSheet.create({
  itemDate: {
    flexDirection: 'row',
    columnGap: 10,
    alignItems: 'center',
  },
  txtDate: {
    fontSize: 14,
    flex: 1, // Giúp text không bị tràn nếu quá dài
  },
});
