import {ICheckInOutRecord} from './interfaces';

// Hàm kiểm tra cùng ngày theo UTC
export const isSameUTCDate = (utcString: string | null) => {
  if (!utcString) return false;
  const dt = new Date(utcString);
  const now = new Date();
  return (
    dt.getUTCFullYear() === now.getUTCFullYear() &&
    dt.getUTCMonth() === now.getUTCMonth() &&
    dt.getUTCDate() === now.getUTCDate()
  );
};

export const getStartOfDayISO = (date = new Date()) => {
  date.setHours(0, 0, 0, 0);
  return date.toISOString();
};
export const getEndOfDayISO = (date = new Date()) => {
  date.setHours(23, 59, 59, 999);
  return date.toISOString();
};
